export default {
	data() {
		return {
			inputValue:'',
			historyArr:[]
		};
	},
	methods:{
		// 获取搜索框内容
		searchText(e){
			console.log(e.detail.value)
			this.inputValue = e.detail.value;
		},
		// 搜索
		search(){
			if(this.inputValue){
				
				// 历史存入
				this.historyArr.push(this.inputValue);
				uni.setStorage({
					key:'history',
					data:JSON.stringify(this.historyArr),
					success:()=>{
						// 清空搜索框
						this.inputValue ='';
					}
				})
				console.log('搜索')
				uni.showToast({
					title:"未查询到该条内容！！！",
					// icon:'none'
				})
			}
		},
		// 清空历史记录
		clearHistory(){
			uni.removeStorage({
			    key: 'history',
				success:()=>{
					this.historyArr = []
				}
			});
		},
		// 返回
		backPage() {


















			// 修复小程序app返回退出应用bug(无法返回重定向至首页)
			var pagelength = getCurrentPages();
			if(pagelength.length===1){
				var path = pagelength[0].route;
				// console.log(path)
				uni.reLaunch({
					url:'/'+path
				})
			}else{
				uni.navigateBack(1)
			}
		},
	},
	onLoad() {
		// 获取本地历史记录
		uni.getStorage({
		    key: 'history',
		    success:  (res)=> {
				if(res.data){
					this.historyArr = JSON.parse(res.data);
				}
		    }
		});
	}
}
